// 封装接口方法
import request from '@/utils/request'
import { getStorage } from '@/utils/storage'
// 登录
export const loginAPI = ({ mobile, code }) =>
  request({
    url: '/v1_0/authorizations',
    method: 'POST',
    data: {
      mobile,
      code
    }
  })

// 用户-刷新token
export const getNewTokenAPI = () => request({
  url: '/v1_0/authorizations',
  method: 'PUT',
  headers: {
    Authorization: 'Bearer ' + getStorage('refresh_token')
  }

})

// 获取所有频道
export const getAllChannelsAPI = () =>
  request({
    url: '/v1_0/channels',
    method: 'GET'
  })

// 获取用户频道
export const getUserChannelsAPI = () =>
  request({
    url: '/v1_0/user/channels',
    method: 'GET'
  })

// 更新用户频道
export const updateUserChannels = (channels) => request({
  url: '/v1_0/user/channels',
  method: 'PUT',
  data: {
    channels: channels
  }
})

// 删除用户指定频道
export const delUserChannels = (target) => request({
  url: `/v1_0/user/channels/${target}`,
  method: 'DELETE'
})

// 获取文章推荐
export const getArticlesAPI = (id, time) =>
  request({
    url: '/v1_0/articles',
    method: 'GET',
    params: {
      channel_id: id,
      timestamp: time
    }
  })

// 对文章不感兴趣
export const articleDislike = (artId) =>
  request({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    data: {
      target: artId
    }
  })

// 举报文章
export const articleReports = (artId, type, remark) =>
  request({
    url: '/v1_0/article/reports',
    method: 'POST',
    data: {
      target: artId,
      type,
      remark
    }
  })

// 搜索联想
export const searchSuggestion = (value) => request({
  url: '/v1_0/suggestion',
  method: 'GET',
  params: {
    q: value
  }
})

// 获取搜索结果
export const searchResult = (q, page, num) => request({
  url: '/v1_0/search',
  method: 'GET',
  params: {
    q: q,
    page: page,
    per_page: num
  }
})

// 获取文章详情
export const getArticleDetail = (artId) => request({
  url: `/v1_0/articles/${artId}`,
  method: 'GET'
})

// 获取用户的关注列表
export const getuserFollowings = () => request({
  url: '/v1_0/user/followings',
  method: 'GET'
})

// 关注用户
export const userFollowing = (autId) => request({
  url: '/v1_0/user/followings',
  method: 'POST',
  data: {
    target: autId
  }
})

// 取消关注
export const unUserFollow = (autId) => request({
  url: `/v1_0/user/followings/${autId}`,
  method: 'DELETE'
})

// 点赞
export const articleLiking = (artId) => request({
  url: '/v1_0/article/likings',
  method: 'POST',
  data: {
    target: artId
  }
})

// 取消点赞
export const UnArticleLike = (artId) => request({
  url: `/v1_0/article/likings/${artId}`,
  method: 'DELETE'
})

// 获取评论
export const getCommentsAPI = ({ artId, offset = null, limit = 10 }) => request({
  url: '/v1_0/comments',
  method: 'GET',
  params: {
    type: 'a',
    source: artId,
    offset,
    limit
  }
})

// 对评论或者回复点赞
export const commentLikingAPI = (comId) => request({
  url: '/v1_0/comment/likings',
  method: 'POST',
  data: {
    target: comId
  }
})

// 取消对评论或者回复点赞
export const unCommentLikingAPI = (comId) => request({
  url: `/v1_0/comment/likings/${comId}`,
  method: 'DELETE'
})

// 对文章发表评论
// axios中 请求体data传参 值为空null，参数不会忽略
// 只有params为空 才会忽略
export const addCommentAPI = ({ artId, content, id = null }) => request({
  url: '/v1_0/comments',
  method: 'POST',
  data: {
    target: artId,
    content,
    art_id: id // 对文章评论回复无需此参数
  }
})

// 个人中心-获取用户信息
export const getUserInfoAPI = () => request({
  url: '/v1_0/user',
  method: 'GET'
})

// 个人中心-获取用户资料
export const getUserProfileAPI = () => request({
  url: '/v1_0/user/profile',
  method: 'GET'
})

// 个人中心-编辑用户照片资料
export const editUserPhotoAPI = (fd) => request({
  url: '/v1_0/user/photo',
  method: 'PATCH',
  data: fd, // 表单对象
  headers: {
    'Content-Type': 'multipart/form-data'
  }
})

// 个人中心-编辑用户个人资料
export const editUserProfileAPI = ({ name, gender, birthday, intro }) => request({
  url: '/v1_0/user/profile',
  method: 'PATCH',
  data: {
    name,
    gender,
    birthday,
    intro
  }

})
