// 封装网络请求 局限性
import theAxios from 'axios'
import router from '@/router'
import Notify from '@/ui/Notify.js'
import { getToken, removeToken } from './token' // setToken
// import { getNewTokenAPI } from '@/api'

const axios = theAxios.create({
  baseURL: 'http://toutiao.itheima.net',
  // baseURL: 'http://geek.itheima.net',
  timeout: 20000
})

// 添加请求拦截器
axios.interceptors.request.use(
  function (config) {
    // 在发送请求之前做些什么
    // 统一携带token
    // ?. 可选链操作符，如果前面对象没有length，整个表达式原地返回undefind
    // console.log(config)
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
  },
  function (error) {
    // 对请求错误做些什么
    return Promise.reject(error)
  }
)

// 添加响应拦截器
axios.interceptors.response.use(
  function (response) {
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    return response
  },
  async function (error) {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    // 401身份认证失败
    if (error.response.status === 401) {
      // Notify({ type: 'warning', message: '身份已过期' })

      // 方式1：清除token,强制跳转到登录界面
      removeToken() // 清除token
      // router.replace('/login')

      // 方式2：无感知刷新token
      //   const res = await getNewTokenAPI()
      //   console.log(res)
      //   setToken(res.data.data.token)
      //   error.config.headers.Authorization = `Bearer ${res.data.data.token}`
      //   // 重新发起报错的
      //   return axios(error.config)
      // } else if (error.response.status === 500 && error.config.url ===
      //   '/v1_0/authorizations' && error.config.method === 'put') { // refresh_token过期
      //   Notify({ type: 'warning', message: '身份已过期' })
      //   localStorage.clear() // 清除token
      //   router.replace('/login')

      // 方式3：没有刷新token接口
      Notify({ type: 'warning', message: '身份已过期' })
      // router.currentRoute 相当于 在vue文件内this.$route ->当前路由对象
      router.replace(`/login?path=${router.currentRoute.fullPath}`)
    }

    return Promise.reject(error)
  }
)

export default ({
  url,
  method = 'GET',
  params = {},
  data = {},
  headers = {}
}) => {
  return axios({
    url,
    method,
    params,
    data,
    headers
  })
}
